import React, { useState, useEffect } from 'react';
// import { FaHandPointLeft } from 'react-icons/fa';
// import { GiRing } from 'react-icons/gi';
import './countdown.css';

function Countdown(){
  const calcTime = () => {
    let endDate = new Date("Oct 21, 2021 13:00:00").getTime();
    let diff = endDate - new Date().getTime();
    let timeLeft = {};

    // if(diff >= 0){
      timeLeft = {
        Days: Math.floor(diff / (1000 * 60 * 60 * 24)),
        Hours: Math.floor(diff % (1000 * 60 * 60 * 24) / (1000 * 60 * 60)),
        Minutes: Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60)),
        Seconds: Math.floor((diff % (1000 * 60)) / 1000)
      }
    // }

    return timeLeft;
  }

const [timeLeft, setTimeLeft] = useState(calcTime());


useEffect(() =>{
  const timer = setTimeout(() => {
    setTimeLeft(calcTime());
    // posStyles.right = posStyles.right + 1;
    // posStyles.left = posStyles.left + 1;
    // console.log("LEFT " + posStyles.left + " right " + posStyles.right);
  }, 1000);
  //Clear timeout if the component is unmounted
  return () => clearTimeout(timer);
});

const timerComponents = [];

Object.keys(timeLeft).forEach((interval) => {
  // if(!timeLeft[interval]){
  //   return;
  // }

  // if(timeLeft[interval] < 10){
  //   timeLeft[interval] = timeLeft[interval];
  // }

  //the extra {" "} puts a space into the code
  timerComponents.push(
    <span key={interval} className="timer__wrap">
      <div className="timer__time">{timeLeft[interval]}</div>
      <div className="mx-1 timer__desc">{interval}{" "}</div>
    </span>
  );
});


return (
    <div>
      <div className="timer d-flex align-items-center justify-content-center">
        {timerComponents.length ? timerComponents : <span>Oh lay oh lay it's wedding time today!</span>}
      </div>
    </div>
  );
}


export default Countdown;
