import React, {useState} from 'react';
import emailjs from 'emailjs-com';
import keys from './apikeys';
import './form.css';
import Popup from './Popup';
import Us4 from './images/us-4.jpg'

function Form(){
  const [contact, setContact]  = useState({
    name: "",
    email: ""
  });

  const [formState, setForm] = useState('noSelection');

  const [showPopUp, setpopUp] = useState('no');

  const formProps = {
    message: ''
  };
  // let message = "";

  // console.log("message 1 " + message);

  function handleChange(event){
    const { name, value } = event.target;
    setContact(prevValue => {
      return {
          ...prevValue,
          [name]: value
        };
      });
    }

  function clearForm(){
    setContact({
      name: "",
      email: ""
    });
  }

  const onSubmit = (e) => {
    e.preventDefault();
    document.body.classList.add('showing-stink');
    if(formState === 'noSelection'){
      setpopUp("yes");
    }else{
      emailjs.sendForm('gmail', keys.TEMPLATE_ID, e.target, keys.USER_ID).then(
        result => {
          clearForm();
          setpopUp("yes");
        },
        error => {
          alert("Well this is embarrassing. Looks like there was a problem, please make sure the details entered are correct and if the problem persists - email us directly evansdyerwedding@gmail.com.");
        });
    }
  }

if(formState === 'noSelection'){
  formProps.message = 'Select one of the options numb nuts';
}
else if(formState === 'No'){
  formProps.message = 'You\'re not coming?! fine then your loss.';
}else{
  formProps.message = 'Yay, you\'re coming! Me and Ollie can\'t wait to spend the day with you.';
}

  return(
      <div>
        <Popup message={formProps.message} handlepopup={(e) => {setpopUp('no'); document.body.classList.remove('showing-stink');}} addclass={`stink__overlay showing ${showPopUp}`} />
        <div className="row no-gutters half-vh">
          <div id="img-col" className="col-12 col-md-6">
            <div className="form__img"></div>
          </div>
          <div className="col-12 col-md-6 form__form">
            <h3 className="mt-5 mb-2 text-center">Can you come?</h3>
            <p className="my-4 text-center px-5 px-md-3">Let us know if you want to come! Once the main site is ready we will let you know via email.<br /> We ask that you leave your kids at home (supervised obvs), we want you to be able to cut loose and you can't do that lugging around your brats.</p>
            <form className="form w-100" onSubmit={onSubmit}>
            <div className="px-5 px-md-4">
              <div className="row flex-column align-items-center">
                <div className="col-10 col-md-8 mb-3">
                  <label htmlFor="attendingYes" className="checkContainer">
                    <span>I can come</span>
                    <input id="attendingYes" className="attRadio" name="attending" type="radio" value="Yes" onChange={(e)=> {setForm(e.target.value); console.log(formState);}} />
                    <span className="customCheck yes"></span>
                  </label>
                </div>
                <div className="col-10 col-md-8">
                  <label htmlFor="attendingNo" className="checkContainer no">
                    <span>I'm a wet lettuce and won't be coming</span>
                    <input id="attendingNo" className="attRadio" name="attending" type="radio" value="No" onChange={(e)=> {setForm(e.target.value); console.log(formState);}} />
                    <span className="customCheck no"></span>
                  </label>
                </div>
              </div>
              <div className="px-3">
                {formState === 'Yes' &&
                <div>
                <p className="text-center my-4">The best decision you'll ever make. Let us know ya deets and we'll give you a buzz so you can pick your food options etc. Peace.</p>
                <div className="row align-items-center justify-content-center">
                  <div class="col-10 col-md-8">
                    <label htmlFor="name" className="w-100 form__label">
                      <span className="formLab sr-only">Your Name:</span>
                      <input id="name" name="name" type="text" placeholder="Your Name" className="form__input" onChange={handleChange} value={contact.name} />
                      <div className="underline"></div>
                    </label>
                  </div>
                  <div class="col-10 col-md-8">
                    <label htmlFor="email" className="w-100 form__label">
                      <span className="formLab sr-only">Email:</span>
                      <input name="email" type="email" placeholder="Your Email" className="form__input" onChange={handleChange} value={contact.email} />
                      <div className="underline"></div>
                    </label>
                  </div>
                </div>
                </div>
                }
                {formState === 'No' &&

                <div className="row align-items-center justify-content-center">
                  <div className="col-10 col-md-10">
                    <p className="text-center my-4">Fine then dont. Please submit your response along with your name (we need to know who to stop speaking to.) and then go rot in heck you old smelly misery guts</p>
                    <label htmlFor="name" className="w-100 form__label">
                      <span className="formLab sr-only">Your Name:</span>
                      <input id="name" name="name" type="text" placeholder="Your Name" className="form__input" onChange={handleChange} value={contact.name} />
                      <div className="underline"></div>
                    </label>
                  </div>
                </div>
                }
              </div>
              <div className="my-4 text-center">
                <button className="form__input--button shadow">Submit</button>
              </div>
              </div>
            </form>
            <div className="form__submitted text-center">
                <p>Thanks for responding we will be in touch. You can now get on with your life.</p>
            </div>
          </div>
        </div>
      </div>
  );
}

export default Form;
